body {
  background-color: #1b192d;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.input-group {
  color: #fff;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 100%;
  font-family: jetbrains mono;
  display: flex;
}

.inputs {
  justify-content: space-between;
  width: 100%;
  display: flex;
  position: absolute;
  top: 2%;
}

input[type="color"] {
  opacity: 0;
  border: none;
  width: 25px;
  height: 25px;
  display: block;
}

#color-picker-wrapper {
  float: left;
  border-radius: 50%;
}

#reset-color {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  height: 25px;
}
